ine-height: 35px;\r\n    box-shadow: 0px 0px 5px rgb(88, 88, 88);\r\n    margin-top: 40px;\r\n    border-radius: 4px;\r\n    transition: 300ms all;\r\n}\r\n\r\n.btn-white:hover{\r\n    background: #444;\r\n    color:white;\r\n}\r\n\r\n#slider.slider-small{\r\n    height: 150px;\r\n}\r\n\r\n#slider.slider-small h1{\r\n    margin-top: 0.2px;\r\n    padding: 0px;\r\n    padding-top: 53px;\r\n}\r\n\r\n#content{\r\n    width: 70%;\r\n    float:left;\r\n    min-height: 650px;\r\n    margin-right: 20px;\r\n}\r\n\r\n#sidebar{\r\n    width: 25%;\r\n    float:left;\r\n}\r\n\r\n.sidebar-item{\r\n    background: #f7f7f7;\r\n    padding: 20px;\r\n    margin-top: 30px;\r\n}\r\n\r\n.sidebar-item .fa-linkedin-square{\r\n    font-size: 4em;\r\n    border-radius:5px;\r\n    color: #0085AE;\r\n    margin-left: 20px;\r\n    text-decoration: none;\r\n}\r\n\r\n.sidebar-item .fa-github{\r\n    font-size: 4em;\r\n    color: black;\r\n\r\n}\r\n\r\n\r\n.sidebar-item h3{\r\n    text-transform: uppercase;\r\n    font-size: 18px;\r\n    margin:0px;\r\n    padding-bottom: 10px;\r\n    margin-bottom: 10px;\r\n    border-bottom: 3px solid #eee;\r\n}\r\n\r\n.sidebar-item .btn{\r\n    margin: 0px auto;\r\n    margin-top: 10px;\r\n}\r\n\r\n.btn{\r\n   display: block;\r\n   background: black;\r\n   color: white;\r\n   padding: 15px; \r\n   font-weight: bold;\r\n   text-transform: uppercase;\r\n   font-size: 14px;\r\n   text-decoration: none;\r\n   max-width: 120px;\r\n   transition: 300ms all;\r\n   border: none;\r\n   cursor:pointer;\r\n   text-align: center;\r\n}\r\n\r\n.btn-success{\r\n    background: rgb(23, 150, 19);\r\n}\r\n\r\n.btn-success:hover{\r\n    background:black;\r\n}\r\n\r\n.btn-warning{\r\n    background: rgb(255, 154, 1);\r\n    color: white !important;\r\n    text-decoration: none;\r\n    \r\n}\r\n\r\n.btn-danger{\r\n    background: rgb(236, 0, 0);\r\n    color: white !important;\r\n    text-decoration: none;\r\n}\r\n\r\n.btn-warning:hover, \r\n.btn-danger:hover {\r\n    transition: